import { Request, Response, Router } from 'express';
import { App } from '../../../App';
import { PapiService } from '../../../services/papi/PapiService';
import { BaseRoute } from '../../BaseRoute';
import { priceTicketRequestSchema } from './priceTicketRequestSchema';

export class PriceTicketRoute extends BaseRoute {

  public getPriceTicket(router: Router): Router {
    return router.get('/', App.validator.query(priceTicketRequestSchema), PriceTicketRoute.getPriceTicketHandler);
  }

  public exportRoutes(): Array<(router: Router) => Router> {
    return [
      this.getPriceTicket,
    ];
  }

  private static async getPriceTicketHandler(req: Request, res: Response): Promise<void> {
    try {
      const response = await PapiService.getPackage(req);
      res.json(response);
    } catch (e) {
      res.status(e.statusCode);
      res.send(e.message);
    }
  }
}
